import React, { useState, useEffect, useCallback } from 'react';
import './PropertyOfferCalculator.css';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ReferenceLine, ResponsiveContainer } from 'recharts';

function PropertyOfferCalculator() {
    const [expectedRent, setExpectedRent] = useState("");
    const [imi, setImi] = useState("");
    const [maintenanceCosts, setMaintenanceCosts] = useState("");
    const [condominiumFee, setCondominiumFee] = useState("");
    const [capRate, setCapRate] = useState("");
    const [calculatedOffer, setCalculatedOffer] = useState(0); // Suggested Offer Value
    const [chartData, setChartData] = useState([]);
    const [loanPercentage, setLoanPercentage] = useState(0); // Percentage of the loan
    const [loanAmount, setLoanAmount] = useState(0); // Nominal loan amount
    const [interestRate, setInterestRate] = useState(""); // Interest rate for the loan
    const [loanDuration, setLoanDuration] = useState("");  // Duration of the loan in years
    const [monthlyPayment, setMonthlyPayment] = useState(0);  // Monthly mortgage payment
    const [acquisitionTax, setAcquisitionTax] = useState(0); // Acquisition tax amount


    function calculateAcquisitionTax(offerValue) {
        if (offerValue <= 97064) {
          return offerValue * 0.01; // 1%
        } else if (offerValue <= 132774) {
          return offerValue * 0.02 - 970.64; // 2% minus parcela a abater
        } else if (offerValue <= 181034) {
          return offerValue * 0.05 - 4953.86; // 5% minus parcela a abater
        } else if (offerValue <= 301688) {
          return offerValue * 0.07 - 8574.54; // 7% minus parcela a abater
        } else if (offerValue <= 578598) {
          return offerValue * 0.08 - 11591.42; // 8% minus parcela a abater
        } else if (offerValue <= 1050400) {
          return offerValue * 0.06; // Flat 6%
        } else {
          return offerValue * 0.075; // Flat 7.5%
        }
      }

    const calculateOffer = useCallback((rate) => {
        const noi = expectedRent - (imi/12 + maintenanceCosts + condominiumFee);
        return (noi*12) / (rate/100);
    }, [expectedRent, imi, maintenanceCosts, condominiumFee]);

    useEffect(() => {
        const newChartData = [];
        for (let rate = 4; rate <= 20; rate += 0.5) {
            const offerValue = calculateOffer(rate);
            const noi = expectedRent - (imi / 12 + maintenanceCosts + condominiumFee);
            const loanAmountAtRate = (loanPercentage / 100) * offerValue; // Calculate the loan amount for this offer value
            const r = interestRate / 12 / 100;  // Monthly interest rate
            const n = loanDuration * 12;  // Total number of payments
            const monthlyPaymentAtRate = loanAmountAtRate && r ? (loanAmountAtRate * r * Math.pow((1 + r), n)) / (Math.pow((1 + r), n) - 1) : 0;  // Calculate the monthly payment for this loan amount
            const acquisitionTax = calculateAcquisitionTax(offerValue);

            // Calculate the yield
            const investment = offerValue - loanAmountAtRate + acquisitionTax;
            const annualCashFlow = (noi - monthlyPaymentAtRate) * 12;
            const yieldAtRate = investment !== 0 ? (annualCashFlow / investment) : 0;
            
            newChartData.push({
                capRate: rate,
                offerValue: offerValue,
                loanAmount: loanAmountAtRate,
                monthlyPayment: monthlyPaymentAtRate,
                yield: yieldAtRate * 100 // Convert to percentage
            });
        }
        setChartData(newChartData);
        setCalculatedOffer(calculateOffer(capRate));
        setAcquisitionTax(calculateAcquisitionTax(calculatedOffer));
    }, [expectedRent, imi, maintenanceCosts, condominiumFee, capRate, calculateOffer, loanPercentage, interestRate, loanDuration, calculatedOffer]);
    

    useEffect(() => {
        setLoanAmount((loanPercentage / 100) * calculatedOffer);
    }, [calculatedOffer, loanPercentage]);
    
    useEffect(() => {
        if (interestRate && loanDuration && loanAmount) {
            const r = interestRate / 12 / 100;  // Monthly interest rate
            const n = loanDuration * 12;  // Total number of payments
            const monthlyPayment = (loanAmount * r * Math.pow((1 + r), n)) / (Math.pow((1 + r), n) - 1);
            setMonthlyPayment(monthlyPayment);
        } else {
            setMonthlyPayment(0);
        }
    }, [loanAmount, interestRate, loanDuration]);
    


    return (
        <div className="global-container2">
            <div className="input-group2">
                <label>Expected Rent:</label>
                <input type="number" value={expectedRent} placeholder="Expected Rent" onChange={e => setExpectedRent(parseFloat(e.target.value))} />

                <label>IMI:</label>
                <input type="number" value={imi} placeholder="IMI" onChange={e => setImi(parseFloat(e.target.value) || 0)} />

                <label>Maintenance Costs:</label>
                <input type="number" value={maintenanceCosts} placeholder="Maintenance Costs" onChange={e => setMaintenanceCosts(parseFloat(e.target.value) || 0)} />

                <label>Condominium Fee:</label>
                <input type="number" value={condominiumFee} placeholder="Condominium Fee" onChange={e => setCondominiumFee(parseFloat(e.target.value) || 0)} />

                <label>Desired Cap Rate:</label>
                <input type="number" value={capRate} placeholder="Desired Cap Rate" onChange={e => setCapRate(parseFloat(e.target.value) || 0)} />
                <input type="range" min="4" max="20" step="0.5" value={capRate} onChange={e => setCapRate(parseFloat(e.target.value) || 0)} />
                
                <label>Loan Amount (% of Offer): {loanPercentage.toFixed(2)}%</label>
                <input type="range" min="0" max="100" value={loanPercentage} onChange={e => setLoanPercentage(parseFloat(e.target.value) || 0)} />

                <label>Interest Rate (%):</label>
                <input type="number" value={interestRate} placeholder="Interest Rate" onChange={e => setInterestRate(parseFloat(e.target.value) || 0)} />

                <label>Loan Duration (years):</label>
                <input type="number" value={loanDuration} placeholder="Loan Duration" onChange={e => setLoanDuration(parseFloat(e.target.value) || 0)} />

            </div>
            <div className="right-container2">
                <div className="chart-container2">
                    <ResponsiveContainer width="90%" height="90%">
                        <LineChart data={chartData} margin={{ top: 5, right: 20, bottom: 20, left: 20 }}>
                        <Line type="monotone" dataKey="offerValue" stroke="#8884d8" name="Offer Value" />
                        <Line type="monotone" dataKey="yield" stroke="#82ca9d" name="Yield" yAxisId="right" />
                        <XAxis dataKey="capRate" label={{ value: 'Cap Rate (%)', position: 'outsideBottom', dy: 15 }} />
                        <YAxis label={{ value: 'Offer Value ()', angle: -90, position: 'insideLeft', offset: -10 }} />
                        <YAxis yAxisId="right" orientation="right" label={{ value: 'Yield (%)', angle: -90, position: 'insideRight', offset: 10 }} />
                        <CartesianGrid stroke="#ccc" />
                        <ReferenceLine x={capRate} stroke="red"/>
                        <ReferenceLine y={0} stroke="#000"  yAxisId="right" />
                        <Tooltip />
                        </LineChart>
                    </ResponsiveContainer>
                </div>

                <div className="metrics-container2">
                    <div className="offer-value">Suggested Offer Value: {calculatedOffer.toFixed(2)}</div>
                    <div className="loan-amount">Loan Amount (): {loanAmount.toFixed(2)}</div>
                    <div className="monthly-payment">Monthly Mortgage Payment: {monthlyPayment.toFixed(2)}</div>
                    <div className="acquisition-tax">Acquisition Tax: {acquisitionTax.toFixed(2)}</div>
                </div>

            </div>

            <div className="table-container">
                <table>
                <thead>
                    <tr>
                    <th>Cap Rate (%)</th>
                    <th>Offer Value ()</th>
                    <th>Loan Amount ()</th>
                    <th>Monthly Payment ()</th>
                    <th>Yield (%)</th>
                    </tr>
                </thead>
                <tbody>
                    {chartData.map((data, index) => (
                    <tr key={index}>
                        <td>{data.capRate.toFixed(2)}</td>
                        <td>{data.offerValue.toFixed(2)}</td>
                        <td>{data.loanAmount.toFixed(2)}</td>
                        <td>{data.monthlyPayment.toFixed(2)}</td>
                        <td>{data.yield.toFixed(2)}</td>
                        
                    </tr>
                    ))}
                </tbody>
                </table>
            </div>

        </div>
    );
}

export default PropertyOfferCalculator;
