import React, { useState } from 'react';
import {AreaChart, PieChart, Pie, Cell, Area, XAxis, YAxis, Tooltip, Legend, CartesianGrid, ResponsiveContainer, ReferenceLine} from 'recharts';
import './PropertyEvaluation.css';

function PropertyEvaluation() {
    const [inputs, setInputs] = useState({
        property_cost: 0,
        renovation_cost: 0,
        borrowed_amount: 0,
        interest_rate: 0,
        loan_duration: 0,
        yearly_insurance: 0,
        expected_rent: 0,
    });
    
    const [monthlyPayment, setMonthlyPayment] = useState(null);
    const [amortizationSchedule, setAmortizationSchedule] = useState([]); 
    const [borrowedPercentage, setBorrowedPercentage] = useState("0");
    const [totalInterest, setTotalInterest] = useState(0);
    const [totalPrincipal, setTotalPrincipal] = useState(0);
    const [totalInsurance, setTotalInsurance] = useState(0);
    const [expectedRentSliderValue, setExpectedRentSliderValue] = useState(0);
    const [capRate, setCapRate] = useState(0);
    const [cashflow, setCashflow] = useState(null);
    const [yieldOnCost, setYieldOnCost] = useState(0);
    const [capRateData, setCapRateData] = useState([]);
    const [isCalculated, setIsCalculated] = useState(false);
    const [validationError, setValidationError] = useState("");

    



    const handleInputChange = (e) => {
        const { name, value } = e.target;
        const totalCost = parseFloat(inputs.property_cost || 0) + parseFloat(inputs.renovation_cost || 0);
    
        if (name === "borrowedPercentage") {
            setBorrowedPercentage(value);
            const borrowedAmount = (value / 100) * totalCost;
            setInputs(prev => ({
                ...prev,
                borrowed_amount: borrowedAmount.toFixed(2)
            }));
        } else if (name === "borrowed_amount") {
            const percentage = (value / totalCost) * 100;
            setBorrowedPercentage(percentage.toFixed(2));
            setInputs(prev => ({
                ...prev,
                [name]: value
            }));
        } else if (name === "expected_rent") {
            let numericValue = parseFloat(value);
            numericValue = isNaN(numericValue) ? 0 : numericValue;
            setExpectedRentSliderValue(numericValue);
            setInputs(prev => ({
                ...prev,
                expected_rent: numericValue
            }));
        } else if (name === "expectedRentSliderValue") {
            const numericValue = parseFloat(value);
            setExpectedRentSliderValue(numericValue);
            setInputs(prev => ({
                ...prev,
                expected_rent: numericValue,
                expectedRentSliderValue: numericValue
            }));
        } else {
            setInputs(prev => ({
                ...prev,
                [name]: value
            }));
        }
    };
    

    const handleSubmit = () => {
            // Check if all input values are zero or null
        const allValuesZero = Object.values(inputs).every(val => !val || parseFloat(val) === 0);
        if (allValuesZero) {
            setValidationError("Input some values so we can go through the calculation.");
            return;  // Exit the function early
        }
        setIsCalculated(true);
        setValidationError("");  // Clear any previous error messages

        fetch('/calculate/monthly-payment', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(inputs)
        })
        .then(response => response.json())
        .then(data => {
            setMonthlyPayment(data.monthly_payment);
        });

        fetch('/calculate/amortization-schedule', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(inputs)
        })
        .then(response => response.json())
        .then(data => {
            setAmortizationSchedule(data.amortization_schedule);

            // Calculate and set the totals
            const totalInterestValue = data.amortization_schedule.reduce((sum, month) => sum + month.interest, 0);
            const totalPrincipalValue = data.amortization_schedule.reduce((sum, month) => sum + month.principal, 0);
            const totalInsuranceValue = data.amortization_schedule.reduce((sum, month) => sum + month.insurance, 0);

            setTotalInterest(totalInterestValue);
            setTotalPrincipal(totalPrincipalValue);
            setTotalInsurance(totalInsuranceValue);
        });
        fetch('/calculate/cashflow', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(inputs)
        })
        .then(response => response.json())
        .then(data => {
            setCashflow(data.cashflow);
        });
        fetch('/calculate-cap-rate', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(inputs)
        })
        .then(response => response.json())
        .then(data => {
            setCapRate(data.cap_rate);  // Assuming you have a state variable called capRate and a corresponding setter setCapRate
        });
        fetch('/calculate-yield-on-cost', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(inputs)
        })
        .then(response => response.json())
        .then(data => {
            setYieldOnCost(data.yield_on_cost);  // Assuming you have a state variable called capRate and a corresponding setter setCapRate
        });
        fetch('/generate-cap-rate-data', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(inputs)  // Assuming 'inputs' contains all the required data
        })
        .then(response => response.json())
        .then(data => {
            setCapRateData(data.cap_rate_data);
        });
        
    };

    return (
        <div className={`global-container ${!isCalculated ? 'global-container-small' : ''}`}>

            <div className="input-group">
                <div>
                    <label>Property Cost ():</label>
                    <input type="number" name="property_cost" placeholder="Property Cost" onChange={handleInputChange} />
                </div>
                <div>
                    <label>Renovation Cost ():</label>
                    <input type="number" name="renovation_cost" placeholder="Renovation Cost" onChange={handleInputChange} />
                </div>
                <div>
                    <label>Borrowed Amount (): <span>{Math.floor(borrowedPercentage)}%</span></label>
                    <input
                        type="number"
                        name="borrowed_amount"
                        placeholder="Borrowed Amount"
                        value={inputs.borrowed_amount ? Math.floor(inputs.borrowed_amount) : ""}
                        onChange={handleInputChange}
                    />
                </div>
                <div>
                    <input
                        type="range"
                        name="borrowedPercentage"
                        min="0"
                        max="100"
                        value={borrowedPercentage}
                        onChange={handleInputChange}
                    />
                </div>
                <div>
                    <label>Interest Rate (%):</label>
                    <input type="number" name="interest_rate" placeholder="Interest Rate" onChange={handleInputChange} />
                </div>
                <div>
                    <label>Loan Duration (years):</label>
                    <input type="number" name="loan_duration" placeholder="Loan Duration" onChange={handleInputChange} />
                </div>
                <div>
                    <label>Yearly Insurance ():</label>
                    <input type="number" name="yearly_insurance" placeholder="Insurance" onChange={handleInputChange} />
                </div>
                <div>
                    <label>Expected Rent (): <span>{Math.floor(expectedRentSliderValue)}</span></label>
                    <input
                        type="number"
                        name="expected_rent"
                        placeholder="Expected Rent"
                        value={inputs.expected_rent ? Math.floor(inputs.expected_rent) : ""}
                        onChange={handleInputChange}
                    />
                </div>
                <div>
                    <input
                        type="range"
                        name="expectedRentSliderValue"
                        min="0"
                        max="3000"
                        value={expectedRentSliderValue}
                        onChange={handleInputChange}
                    />
                </div>
                <div>
                    <label>Condominium Fee ():</label>
                    <input type="number" name="condominium_fee" placeholder="Condominium Fee" onChange={handleInputChange} />
                </div>
                <div>
                    <label>IMI ():</label>
                    <input type="number" name="IMI" placeholder="IMI" onChange={handleInputChange} />
                </div>
                <div>
                    <label>Maintenance Costs ():</label>
                    <input type="number" name="maintenance_costs" placeholder="Maintenance Costs" onChange={handleInputChange} />
                </div>
                <button onClick={handleSubmit}>Calculate</button>
            </div>
            {isCalculated && (
                <>
            <div className="charts-content">
                <div className="top-charts">
                    <div className="pie-chart-container">
                        <ResponsiveContainer width="100%" height="100%">
                            <PieChart margin={{ top: 20, right: 30, left: 20, bottom: 20 }}>
                                <Tooltip formatter={(value) => `${(value).toFixed(0)} `}/>
                                <Legend verticalAlign="top" height={36}/>
                                <Pie
                                    dataKey="value"
                                    data={[
                                        { name: 'Interest', value: totalInterest },
                                        { name: 'Principal', value: totalPrincipal },
                                        { name: 'Insurance', value: totalInsurance }
                                    ]}
                                    label={({ percent }) => `${(percent * 100).toFixed(1)}%`}
                                >
                                    <Cell fill="#ff9900" />
                                    <Cell fill="#0084ff" />
                                    <Cell fill="#4b5dff" />
                                </Pie>
                            </PieChart>
                        </ResponsiveContainer>
                    </div>

                    <div className="area-chart-container">
                        <ResponsiveContainer width="100%" height="100%">
                            <AreaChart margin={{ top: 20, right: 30, left: 20, bottom: 20 }} data={amortizationSchedule}>
                                <CartesianGrid strokeDasharray="3 3" />
                                <XAxis dataKey="month" />
                                <YAxis />
                                <Tooltip 
                                    formatter={(value, name) => {
                                        let formattedName;
                                        switch(name) {
                                            case "interest": formattedName = "Interest"; break;
                                            case "principal": formattedName = "Principal"; break;
                                            case "insurance": formattedName = "Insurance"; break;
                                            default: formattedName = name; 
                                        }
                                        return [`${value.toFixed(1)} `, formattedName];
                                    }}
                                />
                                <Legend 
                                    verticalAlign="top" 
                                    height={36} 
                                    formatter={(value, entry, index) => {
                                        switch(value) {
                                            case "interest": return "Interest";
                                            case "principal": return "Principal";
                                            case "insurance": return "Insurance";
                                            default: return value;
                                        }
                                    }}
                                />
                                <Area type="monotone" dataKey="interest" stackId="1" stroke="#ff9900" fill="#ff9900" />
                                <Area type="monotone" dataKey="principal" stackId="1" stroke="#0084ff" fill="#0084ff" />
                                <Area type="monotone" dataKey="insurance" stackId="1" stroke="#4b5dff" fill="#4b5dff" />
                            </AreaChart>
                        </ResponsiveContainer>
                    </div>
                </div>
                <div className="bottom-charts">
                    <div className="line-chart-container">
                        <ResponsiveContainer width="100%" height="100%">
                            <AreaChart margin={{ top: 20, right: 30, left: 20, bottom: 20 }} data={capRateData}>
                                <XAxis dataKey="expected_rent" />
                                <YAxis />
                                <Tooltip 
                                    formatter={(value, name) => {
                                        let formattedName;
                                        switch(name) {
                                            case "investment_yield": formattedName = "Investment Yield"; break;
                                            case "cap_rate": formattedName = "Cap Rate"; break;
                                            default: formattedName = name; 
                                        }
                                        return [`${value.toFixed(1)}%`, formattedName];
                                    }}
                                />
                                <Legend 
                                    formatter={(value, entry, index) => {
                                        switch(value) {
                                            case "investment_yield": return "Investment Yield";
                                            case "cap_rate": return "Cap Rate";
                                            default: return value;
                                        }
                                    }}
                                />
                                <Area type="monotone" dataKey="investment_yield" stroke="#ff9900" fill="#ff990060" strokeWidth={2} dot={false} />
                                <Area type="monotone" dataKey="cap_rate" stroke="#0084ff" fill="#0084ff48" strokeWidth={2} dot={false} />
                                <ReferenceLine x={inputs.expected_rent} stroke="red" />
                            </AreaChart>
                        </ResponsiveContainer>
                    </div>
                </div>
                <div className="financial_metrics">
                    <div className="container container1">
                        {isCalculated && monthlyPayment && <p>Mortgage Payment: {monthlyPayment.toFixed(2)}</p>}
                        {validationError && <p>{validationError}</p>}
                    </div>
                    
                    <div className="container container2">
                        {isCalculated && cashflow && <p>Monthly Cashflow: {cashflow.toFixed(2)}</p>}
                        {validationError && <p>{validationError}</p>}
                    </div>

                    <div className="container container3">
                        {isCalculated && yieldOnCost && <p>Yield on Cost: {yieldOnCost.toFixed(2)}%</p>}
                        {validationError && <p>{validationError}</p>}
                    </div>

                    <div className="container container4">
                        {isCalculated && capRate && <p>Cap Rate: {capRate.toFixed(2)}%</p>}
                        {validationError && <p>{validationError}</p>}
                    </div>
                </div>
            </div>
            </>
            )}
        </div>
    )
}

export default PropertyEvaluation;
