import React, { useState } from 'react';
import PropertyEvaluation from './components/PropertyEvaluation';
import PropertyOfferCalculator from './components/PropertyOfferCalculator';
import Sidebar from './components/Sidebar';

function App() {
    const [activeComponent, setActiveComponent] = useState('home'); // Default to 'home'

    return (
        <div>
            <Sidebar onNavItemClick={setActiveComponent} />
            {activeComponent === 'home' && <div>Home Content</div>}
            {activeComponent === "Fair Value Calculator" && <PropertyOfferCalculator />}
            {activeComponent === "Fix n' Rent" && <PropertyEvaluation />}
            {/* Other conditions for other nav items can be added here */}
        </div>
    );
}

export default App;