import { useState } from "react";
import "./Sidebar.css";
import 'material-icons/iconfont/material-icons.css';

const navItems = [
    { label: "home", icon: "home" },
    { label: "settings", icon: "settings" },
    { label: "backup", icon: "backup" },
    { label: "Fair Value Calculator", icon: "calculate" },
    { label: "Fix n' Rent", icon: "real_estate_agent" },
    { label: "Fix n' Flip", icon: "construction" }
];

export const Sidebar = (props) => {
    const [isOpen, setIsOpen] = useState(false);
    const [activeNavItem, setActiveNavItem] = useState("home");

    return (
        <nav className={`sidebar ${isOpen ? "open" : ""}`}>
            <div className="sidebar-inner">
                <header className="sidebar-header">
                    <button
                        type="button"
                        className="sidebar-burger"
                        onClick={() => setIsOpen(!isOpen)}
                    >
                        <span className="material-icons-outlined">
                            {isOpen ? "close" : "menu"}
                        </span>
                        
                    </button>
                    <img src={`${process.env.PUBLIC_URL}/logo.svg`} className="sidebar-logo" alt="Logo" />
                </header>
                <nav className="sidebar-menu">
                    {navItems.map((item) => (
                        <button key={item.label} type="button" className={`sidebar-button ${item.label === activeNavItem ? "active" : ""}`} onClick={() => { setActiveNavItem(item.label); if(props.onNavItemClick) props.onNavItemClick(item.label); }}>
                            <span className="material-icons-outlined">{item.icon}</span>
                            <p>{item.label}</p>
                        </button>
                    ))}
                </nav>
            </div>
        </nav>
    );
};

export default Sidebar;
